// Normalize Styles
@import 'node_modules/normalize.css/normalize';

// Import Modules


body {
  font-family: 'montserrat', sans-serif;
  font-weight: 300;
  overflow-x: hidden;
  max-width: 100vw;
}

.base {
  max-height: 100%;
}



@font-face {
  font-family: "icomoon";
  src: url("../fonts/icomoon.woff");
}




@import './_variables';
@import './_helpers';
@import './_grid';
@import './_typography';
@import './_slick';
@import './_nice-select';


@import '../_modules/organisms/header/header';
@import '../_modules/organisms/footer/footer';
@import '../_modules/organisms/hero-section/hero-section';
@import '../_modules/organisms/about-section/about-section';
@import '../_modules/organisms/portfolio-section/portfolio-section';
@import '../_modules/organisms/skills-section/skills-section';
@import '../_modules/organisms/contact-section/contact-section';

// Box sizing partial styles:
// Apply a natural box layout model to all elements
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}


// Styling on warning for users on IE7 or below
.browsehappy {
  text-align: center;
  margin: 0;
  background: #fff8e2;
  color: #000;
  padding: 0.5em 0;
}


.base {
  position: absolute;
  top: 0;
  left: 0;
}
