// about-section styles
.about-section {
  padding: 3em 3em 1em 3em;
  text-align: center;
  background-color: white;
  @include mobile {
    padding: 3em 1em 1em 1em;
  }
  &.animate-in {
    .content {
      .col-4 {
        h1, p, .hero-image {
          transform: translateY(0%);
          opacity: 1;
        }
      }
    }
  }
  p {
    text-align: left;
    font-size: 1.5em;
    line-height: 2em;
  }
  .content {
    padding: 6em 1em;
    background-color: $lightgrey;
    .col-4 {
      position: relative;
      overflow: hidden;
      h1 {
        font-size: 3em;
        text-align: left;
        margin: 0;
        vertical-align: top;
        margin-right: 35%;
        opacity: 0;
        transform: translateX(-100%);
        transition: all 1s cubic-bezier(0.165, 0.84, 0.44, 1);
        @include mobile {
          margin: 20px 0;
        }
        @include tablet {
          text-align: center;
          margin: 20px 0;
        }
      }
      .hero-image {
        width: 100%;
        height: 100%;
        min-height: 400px;
        position: relative;
        background-image: url('/images/portrait.jpg');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        opacity: 1;
        opacity: 0;
        transform: translateX(-100%);
        transition: all 1s cubic-bezier(0.165, 0.84, 0.44, 1) 0.3s;
      }
      p {
        padding: 0em 1em 0em 2em;
        opacity: 0;
        transform: translateX(-100%);
        transition: all 1s cubic-bezier(0.165, 0.84, 0.44, 1) 0.6s;
        @include mobile {
          padding: 1em;
          font-size: 1.2em;
        }
      }
      .red {
        color: $red;
      }
    }

  }

  
}