//media queries helpers

@mixin smallMobile {
	@media (max-width: #{$bpSmallMobile - 1px}) {
		@content;
	}
}


@mixin mobile {
	@media (max-width: #{$bpMaxMobile - 1px}) {
		@content;
	}
}

@mixin tablet {
	@media (min-width: #{$bpMaxMobile}) and (max-width: #{$bpMinDesktop - 1px}) {
		@content;
	}
}

@mixin desktop {
	@media (min-width: #{$bpMinDesktop}) {
		@content;
	}
}


@mixin lgDesktop {
	@media (min-width: #{$bpMinLgDesktop}) {
		@content;
	}
}

@mixin objectfit-cover {
	width: 100%;
	height: 100%;
	object-fit: cover;
	font-family: "object-fit: cover;";
}

@mixin objectfit-contain {
	width: 100%;
	height: 100%;
	object-fit: contain;
	font-family: "object-fit: contain;";
}


@function calc-em($target) {
	@return $target / 16px * 1em;
}

%clearfix {
	&:before, &:after {
		display: table;
		content: " ";
	}
	&:after {
		clear: both;
	}
}

.clearfix {
	@extend %clearfix;
}

.vh {
	border: 0;
	clip: rect(0 0 0 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}

.padding-s {
	padding: 10px !important;
}
.padding-m {
	padding: 30px !important;
}
.padding-l {
	padding: 20px !important;
	@include desktop() {
		padding: 60px !important;
	}
}

.cta-center {
	display: block;
	text-align: center;
	padding: 20px;
}

.content-wrapper {
	position: relative;
	z-index: 1;
	//transform: translateY(-290px);
	margin-top: -260px;
	display: block;
	padding-bottom: 60px;
	.container {
		padding: 0px 30px;
	}
}

.content-container {
	margin-top: 25px;
	background-color: white;
	display: block;
	position: relative;
	padding: 30px 30px 50px 30px; 
	box-shadow: 0px 0px 40px rgba(0,0,0,0.4);
	border-radius: 5px;
	margin-bottom: 40px;
	line-height: 1.5;
	min-height: 400px;
	@include mobile {
		padding: 30px;
	}
	// h1,h2,h3,h4,h5,h6 {
	// 	margin-top: 0;
	// }
}

.float-left {
	float: left;
}