// header styles
.header {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
  &.scrolled {
    background-color: white;
    .site-logo, .site-nav {
      padding: 1em 3em;
      @include mobile {
        padding: 1em;
      }
    }
  }
  .site-logo {
    float: left;
    padding: 2em 3em;
    transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
    img {
      width: 60px;
    }
    @include mobile {
      padding: 1em;
    }
  }
  .site-nav {
    float: right;
    padding: 2em 3em;
    transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
    @include mobile {
      padding: 1em;
    }
    span {
      width: 35px;
      margin-bottom: 8px;
      height: 3px;
      background-color: #2a2a2a;
      display: block;
      transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
      &.top {
        margin-top: 3px;
      }
    }
    &.open {
      .top {
        transform: translateY(11px) rotate(45deg);
      }
      .bottom {
        transform: translateY(-11px) rotate(-45deg);
      }
      .middle {
        transform: rotate(45deg);
      }

    }
  }
  .site-nav-content {
    width: 100%;
    height: 100vh;
    position: fixed;
    z-index: -1;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
    transform: translateX(-100%);
    opacity: 0;
    &.showing {
      opacity: 1;
      transform: translateX(0);
      ul {
        padding: 0;
        li {
          opacity: 1;
          transform: translateY(0px);
        }
      }
    }
    ul {
      text-align: center;
      padding: none;
      list-style-type: none;
      li {
        display: block;
        padding: 1em;
        font-weight: bold;
        font-size: 1.5em;
        opacity: 0;
        transition: all 0.8s cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
        transform: translateY(50px);
        a {
          position: relative;
          color: black;
          text-decoration: none;
          overflow: hidden;
          display: inline-block;
          &::after {
            content: '';
            display: block;
            width: 100%;
            height: 3px;
            background-color: $red;
            transform: translateX(-100%);
            opacity: 0;
          }
          &:hover {
            &::after {
              transform: translateX(0);
              opacity: 1;
              transition: all 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
            }
          }
        }
      }
    }
  }
}