/* muli-200 - latin */
@font-face {
  font-family: 'Muli';
  font-style: lighter;
  font-weight: 200;
  src: url('../fonts/muli-v11-latin-200.woff'); /* IE9 Compat Modes */
}
/* muli-300 - latin */
@font-face {
  font-family: 'Muli';
  font-style: light;
  font-weight: 300;
  src: url('../fonts/muli-v11-latin-300.woff'); /* IE9 Compat Modes */
}
/* muli-regular - latin */
@font-face {
  font-family: 'Muli';
  font-style: regular;
  font-weight: 400;
  src: url('../fonts/muli-v11-latin-regular.woff'), /* IE9 Compat Modes */
}
/* muli-600 - latin */
@font-face {
  font-family: 'Muli';
  font-style: semibold;
  font-weight: 600;
  src: url('../fonts/muli-v11-latin-600.woff'); /* IE9 Compat Modes */
}
/* muli-700 - latin */
@font-face {
  font-family: 'Muli';
  font-style: bold;
  font-weight: 700;
  src: url('../fonts/muli-v11-latin-700.woff'); /* IE9 Compat Modes */
}
/* muli-800 - latin */
@font-face {
  font-family: 'Muli';
  font-style: black;
  font-weight: 800;
  src: url('../fonts/muli-v11-latin-800.woff'); /* IE9 Compat Modes */
}
/* muli-900 - latin */
@font-face {
  font-family: 'Muli';
  font-style: heavy;
  font-weight: 900;
  src: url('../fonts/muli-v11-latin-900.woff'); /* IE9 Compat Modes */
}



@font-face {
  font-family: 'icomoon';
  font-style: normal;
  font-weight: 300;
  src: url('../fonts/icomoon.woff'); /* IE9 Compat Modes */
}






//base sizes
$base-fontsize: 16px;

$primaryfont: 'Muli', Arial, Helvetica, sans-serif;
$icon: 'icomoon';


//colors
$red: #e02e2e;
$purple: #6e358e;
$grey: #494949;
$lightgrey: #f7f7f7; 
$mediumgrey: #eeecec;
$pink: #ecbeca;
$main-gradient:  linear-gradient(to top right, $red, $purple);



$blue: #0078ca;
$lightblue: rgba(0, 120, 202, 0.1);
$mediumblue: rgba(0, 120, 202, 0.25);

$green: #00a259;
$lightgreen: rgba(0, 162, 89, 0.1);
$mediumgreen: rgba(0, 162, 89, 0.25);

$pink: #da355d;
$lightpink: rgba(218, 53, 93, 0.1);
$mediumpink: rgba(218, 53, 93, 0.25);

//transitions

$quick: 0.3s ease-in-out;


//shadows

$shadow-small: 3px 3px 5px rgba(0,0,0,0.1);
$shadow: 10px 10px 20px rgba(0,0,0,0.2);


// Breakpoints
$bpSmallMobile: 384px;
$bpMaxMobile: 767px;
$bpMinDesktop: 1025px;
$bpMinLgDesktop: 1150px;
