// Default variables
$font_size: 1em !default;
$font_size_small: 0.8em !default;

$input_border_radius: 50px !default;
$input_height: 42px !default;
$input_height_small: 36px !default;
$dropdown_padding: 18px !default;

$gray_dark: #444 !default;
$gray: #999 !default;
$gray_light: #e8e8e8 !default;
$gray_lighter: #f6f6f6 !default;
$primary_light: $gray !default;
$arrow_color: $gray !default;

// Style the dropdown
.nice-select {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: #fff;
  border-radius: $input_border_radius;
  border: solid 1px $gray_light; 
  box-sizing: border-box;
  clear: both;
  cursor: pointer;
  display: inline-block;
  color: $purple;
  margin: 5px 10px;
  //float: left;
  font-family: inherit;
  font-size: $font_size;
  font-weight: 600;
  height: auto;
  line-height: 1.5;
  outline: none;
  padding-left: $dropdown_padding;
  padding-right: $dropdown_padding + 40;
  position: relative;
  text-align: left !important;
  transition: all 0.2s ease-in-out;
  user-select: none;
  width: 300px;
  padding: 10px 20px;
  z-index: 1;
  &:hover {
    box-shadow: 0px 0px 40px rgba(0,0,0,0.3);
  }
  &:active, &:focus {
    box-shadow: 0px 0px 40px rgba(0,0,0,0.3);
  }
  // Arrow
  &:after { 
    // border-bottom: 2px solid $arrow_color;
    // border-right: 2px solid $arrow_color;
    content: '\e903';
    font-family: $icon;
    position: absolute;
    right: 20px;
    font-size: 0.7em;
    transform: rotate(90deg);
    transition: all 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
    top: calc(50% - 0.7em);
  }
  
  &.open {
    @extend :active;
    border-radius: 30px 30px 0px 0px;
    &:after {
      transform: rotate(-90deg);
    }
    .list {
      opacity: 1;
      pointer-events: auto;
      transform: scale(1) translateY(0);
    }
  }
  &.disabled {
    border-color: lighten($gray_light, 2%);
    color: $gray;
    pointer-events: none;
    &:after { 
      border-color: lighten($arrow_color, 20%);
    }
  }
  
  // Modifiers
  &.wide {
    width: 100%;
    .list {
      left: 0 !important;
      right: 0 !important;
    }
  }
  &.right {
    float: right;
    .list {
      left: auto;
      right: 0;
    }
  }
  &.small {
    font-size: $font_size_small;
    height: $input_height_small;
    line-height: $input_height_small - 2;
    &:after { 
      height: 4px;
      width: 4px;
    }
    .option {
      line-height: $input_height_small - 2;
      min-height: $input_height_small - 2;
    }
  }
  
  // List and options
  .list {
    background-color: #fff;
    border-radius: 0px 0px 30px 30px;
    box-shadow: 0 0 0 1px rgba($gray_dark, .11);
    box-sizing: border-box;
    margin-top: 0px;
    opacity: 0;
    overflow: hidden;
    padding: 0;
    pointer-events: none;
    position: absolute;
    top: 100%; left: -1px;
    transform-origin: 50% 0;
    transform: scale(.75) translateY(- $input_height / 2);
    transition: all .2s cubic-bezier(0.5, 0, 0, 1.25), opacity .15s ease-out;
    width: 300px;
    &:hover .option:not(:hover) {
      background-color: transparent !important;
    }
  }
  .current {
    width: 100%;
    height: auto;
  }
  .option {
    cursor: pointer;
    font-weight: 400;
    line-height: 1.5;
    list-style: none;
    min-height: $input_height - 2;
    outline: none;
    padding-left: $dropdown_padding;
    padding-right: $dropdown_padding + 11;
    text-align: left;
    transition: all 0.2s;
    width: 100%;
    height: 100%;
    padding-top: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid $lightgrey;
    color: $grey;
    &:hover, &.focus, &.selected.focus { 
      background-color: $gray_lighter;
    }
    &.selected { 
      font-weight: bold;
      color: $purple;
    }
    &.disabled {
      background-color: transparent;
      color: $gray;
      cursor: default;
    }
  }
}

// Use display instead of opacity for IE <= 10
.no-csspointerevents .nice-select {
  .list {
    display: none;
  }
  &.open {
    .list {
      display: block;
    }
  }
}