// contact-section styles
.contact-section {
    padding: 6em 3em;
    background-color: $lightgrey;
    text-align: center;
    @include mobile {
        padding: 6em 1em;
      }
    h1 {
        font-size: 2.5em;
    }
    p {
        font-size: 1em;
        font-weight: 300;
        margin: 0;
        line-height: 2em;
    }
    a {
        text-decoration: none;
        color: black;
        font-weight: bold;
        border-bottom: 3px solid $red;
        font-size: 1em;
    }
    .contact-item {
        padding-top: 20px;
    }
}