// portfolio-section styles
.portfolio-section {
  padding: 0em 0em;
  background-color: white;
  .col-4, .col-8 {
    padding: 1em;
    height: 450px;
  }
  .title-box {
    background-color: $red;
    display: block;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    h1 {
      font-size: 3em;
    }
  }
  .image-placeholder {
    width: 100%;
    height: 100%;
    display: block;
    background-color: $lightgrey;
    position: relative;
    text-decoration: none;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &:hover {
      .portfolio-detail {
        opacity: 0.87;
        transform: scale(1);
      }
    }
  }
  .items-wrapper {
    padding: 0 2em;
    @include mobile {
      padding: 0 1em;
    }
  }
  .portfolio-detail {
    display: block;
    width : calc(100% - 60px);
    height: calc(100% - 60px);
    opacity: 0;
    position: absolute;
    background-color: $red;
    top:30px;
    left: 30px;
    color: white;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    opacity: 0;
    transform: scale(0);
    transition: all 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
    padding: 2em;
    p {
      line-height: 2em;
    }
  }
  .portfolio-item {
    opacity: 0;
    transform: translateY(150px);
    transition: all 1s cubic-bezier(0.165, 0.84, 0.44, 1) 0.3s;
    &.animate-in {
      transform: translateY(0);
      opacity: 1;
    }
  }
  .section-head {
    @include tablet {
      height: 300px;
    }
  }
}