// skills-section styles
.skills-section {
    padding: 6em 3em;
    @include mobile {
      padding: 1em;
    }
    .skill-bars {
        padding: 30px;
        @include mobile {
            padding: 0;
        }
        .title {
            padding-top: 10px;
            font-weight: 600;
            font-size: 0.8em;
        }
        .progress {
            margin: 20px 0px; 
            display: block;
            width: 100%;
            background-color: $lightgrey;
            height: 3px;
            position: relative;
            .progress-bar {
                display: block;
                position: absolute;
                height: 3px;
                background-color: $red;
            }
        }
    }
}
.red {
    color: $red;
}