/* =======================================================================
01 Global
========================================================================== */

$numberCols: 12;
$column: 100/$numberCols;
.col {
	@for $i from 1 through $numberCols/4 {
		&-#{$i} {
			display: inline-block;
			position: relative;
			vertical-align: top;
			margin-right: -5px;
			width: 100%;
			padding: 10px;
		}
	}

	@for $i from $numberCols/4 + 1 through $numberCols {
		&-#{$i} {
			display: inline-block;
			position: relative;
			vertical-align: top;
			margin-right: -5px;
			width: 100%;
			padding: 12px;
		}
	}

	// @for $i from 1 through $numberCols {
	// 	&-offset-#{$i} {
	// 		margin-left: 0;
	// 	}
	// }

	@include tablet {
		@for $i from 1 through $numberCols/3 {
			&-#{$i} {
				width: min(($column * $i * 2) * 1%, 33.33%);
			}
			&-3 {
				width: 50%;
			}
		}
	}

	@include desktop {
		@for $i from 1 through $numberCols {
			&-#{$i} {
				width: ($column * $i) * 1%;
			}
		}
	}

}





/* =====================================================
		Layout
========================================================*/
section {
	position:relative;
	margin: 0;
	padding: 0;
	width: 100%;
	height: auto;
}

.container {
	position: relative!important; 	// Fix for full screen BG images using an img tag and object-fit
	max-width: 1600px;
	padding: 0 20px;
	margin: 0 auto;
	@include desktop {
		padding: 0 20px;
	}

	@extend %clearfix;

	&--full {
		padding: 0;
		max-width: none;
	}

	&--mw720 {
		max-width: 720px !important;
	}
	&--mw960 {
		max-width: 960px !important;
	}
	&--mw1024 {
		max-width: 1024px !important;
	}
	&--mw1140 {
		max-width: 1140px !important;
	}
	&--mw1280 {
		max-width: 1280px !important;
	}
	&--mw1320 {
		max-width: 1320px !important;
	}
	&--mw1440 {
		max-width: 1440px !important;
	}

}

%container {
	position: relative;
	width: 100%;
	height: 100%;
	margin: auto;
	@extend %clearfix;
}


.row {
	width: 100%;
	height: 100%;
	&:before, &:after {
		display: table;
		content: " ";
	}
	&:after {
		clear: both;
	}
}

.tablet-l {
	@include tablet {
		width: 100%;
	}
}

.tablet-m {
	@include tablet {
		width: 50%;
	}
}