}

.t-right {
  text-align: right;
}

.t-center {
  text-align: center;
}

.t-with-square, .t-with-circle {
  position: relative;
  z-index: 1;
  font-size: 1.95em;
  color: #6e358e;
}

.t-with-square::before, .t-with-circle::before {
  content: '';
  display: block;
  width: 3em;
  height: 3em;
  border: 1px solid #da355d;
  position: absolute;
  top: -1em;
  left: -1.5em;
  transform: rotate(-45deg);
  z-index: -1;
  animation: rotate 30s linear infinite forwards;
  opacity: 0.3;
}

.t-with-circle::before {
  border-radius: 50%;
  animation: scale 3s cubic-bezier(0.075, 0.82, 0.165, 1) infinite alternate;
}

.t-inline-block {
  display: inline-block;
  padding-right: 10px;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes scale {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.2);
  }
}

h1 {
  font-size: 3em;
}

@media (max-width: 766px) {
  h1 {
    font-size: 2em;
  }
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-slider:hover .slick-next {
  transform: translateX(0);
  opacity: 1;
}

.slick-slider:hover .slick-prev {
  transform: translateX(0);
  opacity: 1;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

/* Slider */
.slick-loading .slick-list {
  background: #fff url("/Cwp/assets/facsmab/images/ajax-loader.gif") center center no-repeat;
}

/* Icons */
@font-face {
  font-family: "slick";
  src: url("/Cwp/assets/facsmab/fonts/slick.eot");
  src: url("/Cwp/assets/facsmab/fonts/slick.eot?#iefix") format("embedded-opentype"), url("/Cwp/assets/facsmab/fonts/slick.woff") format("woff"), url("/Cwp/assets/facsmab/fonts/slick.ttf") format("truetype"), url("/Cwp/assets/facsmab/fonts/slick.svg#slick") format("svg");
  font-weight: normal;
  font-style: normal;
}

/* Arrows */
.slick-prev,
.slick-next {
  z-index: 10;
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
}

.slick-prev:hover, .slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}

.slick-prev:hover:before, .slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0.3;
}

.slick-prev:before,
.slick-next:before {
  display: block;
  width: 30px;
  color: white;
  content: "";
  opacity: 0.75;
  transition: opacity 0.3s ease-in-out;
}

.slick-prev {
  width: 70px;
  height: auto;
  position: absolute;
  left: 5%;
  transform: translateX(-50px);
  opacity: 0;
  transition: all 0.3s ease-in-out;
}

.slick-prev:before {
  top: 0;
  left: 0;
  content: "4";
  font-family: "ETmodule";
  font-size: 70px;
  position: absolute;
}

.slick-next {
  width: 70px;
  height: auto;
  position: absolute;
  right: 5%;
  transform: translateX(50px);
  opacity: 0;
  transition: all 0.3s ease-in-out;
}

.slick-next:before {
  top: 0;
  left: 0;
  content: "5";
  font-family: "ETmodule";
  font-size: 70px;
  position: absolute;
}

/* Dots */
.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  position: absolute;
  top: 200px;
  list-style: none;
  display: block;
  text-align: left;
  padding: 0;
  padding-left: 10%;
  margin: 0;
  width: 100%;
}

@media (max-width: 766px) {
  .slick-dots {
    top: 100%;
    left: 50%;
    padding-left: 0px;
    margin-left: -20px;
  }
}

.slick-dots li {
  position: relative;
  display: inline-block;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}

.slick-dots li button {
  border: 0;
  background: transparent;
  display: block;
  outline: none;
  line-height: 0px;
  font-size: 0px;
  color: transparent;
  padding: 5px;
  cursor: pointer;
}

.slick-dots li button:hover, .slick-dots li button:focus {
  outline: none;
}

.slick-dots li button:hover:before, .slick-dots li button:focus:before {
  opacity: 1;
}

.slick-dots li button:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "•";
  font-family: "slick";
  font-size: 35px;
  line-height: 20px;
  text-align: center;
  color: #494949;
  opacity: 0.8;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-dots li.slick-active button:before {
  color: #6e358e;
  opacity: 0.75;
}

.nice-select {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: #fff;
  border-radius: 50px;
  border: solid 1px #e8e8e8;
  box-sizing: border-box;
  clear: both;
  cursor: pointer;
  display: inline-block;
  color: #6e358e;
  margin: 5px 10px;
  font-family: inherit;
  font-size: 1em;
  font-weight: 600;
  height: auto;
  line-height: 1.5;
  outline: none;
  padding-left: 18px;
  padding-right: 58px;
  position: relative;
  text-align: left !important;
  transition: all 0.2s ease-in-out;
  user-select: none;
  width: 300px;
  padding: 10px 20px;
  z-index: 1;
}

.nice-select:hover {
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.3);
}

.nice-select:active, .nice-select.open, .nice-select:focus {
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.3);
}

.nice-select:after {
  content: '\e903';
  font-family: "icomoon";
  position: absolute;
  right: 20px;
  font-size: 0.7em;
  transform: rotate(90deg);
  transition: all 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
  top: calc(50% - 0.7em);
}

.nice-select.open {
  border-radius: 30px 30px 0px 0px;
}

.nice-select.open:after {
  transform: rotate(-90deg);
}

.nice-select.open .list {
  opacity: 1;
  pointer-events: auto;
  transform: scale(1) translateY(0);
}

.nice-select.disabled {
  border-color: #ededed;
  color: #999;
  pointer-events: none;
}

.nice-select.disabled:after {
  border-color: #cccccc;
}

.nice-select.wide {
  width: 100%;
}

.nice-select.wide .list {
  left: 0 !important;
  right: 0 !important;
}

.nice-select.right {
  float: right;
}

.nice-select.right .list {
  left: auto;
  right: 0;
}

.nice-select.small {
  font-size: 0.8em;
  height: 36px;
  line-height: 34px;
}

.nice-select.small:after {
  height: 4px;
  width: 4px;
}

.nice-select.small .option {
  line-height: 34px;
  min-height: 34px;
}

.nice-select .list {
  background-color: #fff;
  border-radius: 0px 0px 30px 30px;
  box-shadow: 0 0 0 1px rgba(68, 68, 68, 0.11);
  box-sizing: border-box;
  margin-top: 0px;
  opacity: 0;
  overflow: hidden;
  padding: 0;
  pointer-events: none;
  position: absolute;
  top: 100%;
  left: -1px;
  transform-origin: 50% 0;
  transform: scale(0.75) translateY(-21px);
  transition: all 0.2s cubic-bezier(0.5, 0, 0, 1.25), opacity 0.15s ease-out;
  width: 300px;
}

.nice-select .list:hover .option:not(:hover) {
  background-color: transparent !important;
}

.nice-select .current {
  width: 100%;
  height: auto;
}

.nice-select .option {
  cursor: pointer;
  font-weight: 400;
  line-height: 1.5;
  list-style: none;
  min-height: 40px;
  outline: none;
  padding-left: 18px;
  padding-right: 29px;
  text-align: left;
  transition: all 0.2s;
  width: 100%;
  height: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #f7f7f7;
  color: #494949;
}

.nice-select .option:hover, .nice-select .option.focus, .nice-select .option.selected.focus {
  background-color: #f6f6f6;
}

.nice-select .option.selected {
  font-weight: bold;
  color: #6e358e;
}

.nice-select .option.disabled {
  background-color: transparent;
  color: #999;
  cursor: default;
}

.no-csspointerevents .nice-select .list {
  display: none;
}

.no-csspointerevents .nice-select.open .list {
  display: block;
}

