// hero-section styles
.hero-section {
  position: relative;
  text-align: center;
  h1 {
    font-size: 8em;
    padding: 1em 1em 0em 1em;
    margin: 0;
    @include mobile {
      font-size: 4em;
      padding-top: 2em;
    }
    .red {
      color: $red;
    }
  }
  h2 {
    font-size: 4em;
    font-weight: 300;
    padding: 1em 1em 0em 1em;
    strong {
      font-weight: 600;
      border-bottom: 5px solid $red; 
    }
  }
  .hero-text {
    h1 {
      transition: all 1s cubic-bezier(0.165, 0.84, 0.44, 1);
    }
    p {
      transition: all 1s cubic-bezier(0.165, 0.84, 0.44, 1) 0.3s;
    }
    h1, p {
      opacity: 0;
      transform: translateY(100px);
    }

    &.animate-in {
      h1, p {
        opacity: 1;
        transform: translateY(0);
      }
    }
  }
  .bg-text {
    position: absolute;
    z-index: -1;
    display: block;
    width: 100vw;
    top: -20%;
    overflow: hidden;
    @include mobile {
      top:-5%;
    }
    span {
      left: -10%;
      font-size: 40vw;
      font-weight: 700;
      color: rgba(0,0,0,0.05);
      line-height: 0.5;
      position: relative;
    }
  }
  .container {
    padding-bottom: 3em;
  }

}

#particles-js {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: transparent;
  background-image: url("");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}


.social-links {
  display: block;
  width: 100%;
  text-align: center;
  opacity: 0;
  transform: translateY(100px);
  transition: all 1s cubic-bezier(0.165, 0.84, 0.44, 1) 0.6s;
  &.animate-in {
    opacity: 1;
    transform: translateY(0);
  }
  ul {
    margin-top: 4em;
    padding: 0;
    list-style-type: none;
    li {
      display: inline-block;
      a {
        display: block;
        text-decoration: none;
        padding: 10px; 
        span {
          font-size: 1.5em;
          font-family: 'icomoon';
          color: #2a2a2a;
          &:hover {
            color: $red;
          }
        }
      }
    }
  }

  
}