

.t-left {
  text-align: left;
}

.t-right {
  text-align: right;
}

.t-center {
  text-align: center;
}
.t-with-square , .t-with-circle {
  position: relative;
  z-index: 1;
  font-size: 1.95em;
  color: $purple;
  &::before {
    content:'';
    display: block;
    width: 3em;
    height: 3em;
    border: 1px solid $pink;
    position: absolute;
    top: -1em;
    left: -1.5em;
    transform: rotate(-45deg);
    z-index: -1;
    animation: rotate 30s linear infinite forwards;
    opacity: 0.3;
  }
}
.t-with-circle {
  &::before {
    border-radius: 50%;
    animation: scale 3s cubic-bezier(0.075, 0.82, 0.165, 1) infinite alternate;
  }
}

.t-inline-block {
  display: inline-block;
  padding-right: 10px;
}


@keyframes rotate {
  from {transform: rotate(0deg);}
  to {transform: rotate(360deg);}
}

@keyframes scale {
  from {transform: scale(1);}
  to {transform: scale(1.2);}
}

h1 {
  font-size: 3em;
  @include mobile {
    font-size: 2em;
  }
}